<template>
  <div id="news_detail">
    <h1>最新消息</h1>
    <div class="news_detail__container">
      <div class="this__title">{{this_news.title}}</div>
      <div class="this__date">{{this_news.date}}</div>
      <hr class="this__hr">
      <div class="this__description"
      v-html="this_news.description"></div>
      <div class="this__banner">
        <img :src="this_news.banner" alt="">
      </div>
      <div class="this__content"
      v-html="this_news.content"></div>
      <div class="this__remark"
      v-html="this_news.remark"></div>
      <div class="news_detail__control">
        <a href="#" class="newer"
        title="新一則"
        v-if="those_news.newer.is_shown"
        @click="go_page(`front/news_detail/${those_news.newer.id}`)">
          <img src="../../assets/icon/page_num_nav_arrow_left.png" alt="">
        </a>
        <a href="#" class="back"
        title="回到列表"
        @click="go_page('front/news')">BACK</a>
        <a href="#" class="older"
        title="舊一則"
        v-if="those_news.older.is_shown"
        @click="go_page(`front/news_detail/${those_news.older.id}`)">
          <img src="../../assets/icon/page_num_nav_arrow_right.png" alt="">
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  watch: {
    $route() {
      this.create_this_news();
    },
  },
  props: ['news'],
  // {
  //   id: '',
  //   title: '',
  //   date: '',
  //   description: '',
  //   banner: '',
  //   content: '',
  //   remark: '',
  // },
  data() {
    return {
      this_news: {},
      those_news: {
        newer: {
          is_shown: true,
          id: '',
        },
        older: {
          is_shown: true,
          id: '',
        },
      },
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    create_this_news() {
      this.this_news = this.news
        .find((item) => item.id === this.$route.params.id);
      const theIndex = this.news.findIndex((item) => item.id === this.$route.params.id);
      if (theIndex === 0) {
        // 已經是最新的
        this.those_news.newer.is_shown = false;
      } else {
        this.those_news.newer.is_shown = true;
        this.those_news.newer.id = this.news[theIndex - 1].id;
      }
      if (theIndex === this.news.length - 1) {
        // 已經是最舊的
        this.those_news.older.is_shown = false;
      } else {
        this.those_news.older.is_shown = true;
        this.those_news.older.id = this.news[theIndex + 1].id;
      }
    },
  },
  created() {
    this.create_this_news();
  },
};
</script>
